import { Component, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { MaDataGridColumnOptions } from '../../interfaces/ma-data-grid-options';
import { MaDataGridComponent } from '../../ma-data-grid.component';

@Component({
  selector: 'ma-data-grid-cell-selector',
  templateUrl: './data-grid-cell-selector.component.html',
  styleUrls: ['./data-grid-cell-selector.component.css']
})
export class DataGridCellSelectorComponent implements OnInit, OnChanges {

  @ViewChild("myInputSelectorBox", { static: false }) myInputSelectorBox: ElementRef;
  title: string = '';
  @Output() dataChange = new EventEmitter<any>();
  @Input() data: any;
  @Input() isHeader: boolean = false;
  @Input() col: MaDataGridColumnOptions;
  @Input() prop: string;
  @Input() myGrid: MaDataGridComponent;

  constructor() { }

  ngOnInit(): void {

    /* Cas du header pas de prop */
    if(this.isHeader) {
      this.title = this.col.title;
    } else {
      this.title = '';
    }
    
    if (this.data && (this.data[this.prop] == true || this.data[this.prop] == 1 || this.data[this.prop] == "on")) {
      this.data[this.prop] = true;
    }
  }

  onChangeCheckbox() {
    if(this.isHeader) {
      for (let row of this.data) {
        row[this.prop] = this.myInputSelectorBox.nativeElement.checked;
      }
    } else {
      this.data[this.prop] = this.myInputSelectorBox.nativeElement.checked;
      console.log('EMIT SELECTOR dataChange', this.data);
    }
   
    this.dataChange.emit(this.data);
    if (this.myGrid != null) {
      this.myGrid._dataSelector(this.data,this.prop);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    console.log('SELECTOR ngOnChanges',changes)
  }
}
